<script>
  import { onMount, onDestroy } from "svelte"
  import { fade, fly } from "svelte/transition"
  import { setupApp } from "../stores/store.js"

  export let id
  export let mode

  let goAnimation = false
  let ruby

  onMount(() => {
    ruby.style.left = `${mode.x * setupApp.size}vmin`
    ruby.style.top = `${mode.y * setupApp.size}vmin`
    setTimeout(() => (goAnimation = true), Math.random() * 5000)
  })
</script>

<style>
  .ruby {
    position: absolute;
    background: url("./assets/diamond.svg") no-repeat center center;
    width: 5vmin;
    height: 5vmin;
    transition: animation 1s;
  }
  .show {
    -webkit-animation: filter-animation 3s infinite;
    animation: filter-animation 3s infinite;
  }

  @-webkit-keyframes filter-animation {
    0% {
      -webkit-filter: saturate(3) hue-rotate(0deg);
    }
    100% {
      -webkit-filter: saturate(3) hue-rotate(360deg);
    }
  }
  @keyframes filter-animation {
    0% {
      filter: saturate(3) hue-rotate(0deg);
    }
    100% {
      filter: saturate(3) hue-rotate(360deg);
    }
  }
</style>

<div
  class="ruby {goAnimation ? 'show' : ''}"
  in:fly="{{ delay: Math.random() * 750, duration: 700, x: Math.sign(Math.random() - 0.5) * 25, y: Math.sign(Math.random() - 0.5) * 25 }}"
  out:fly="{{ duration: 1000, x: 50, y: -50 }}"
  bind:this="{ruby}"
></div>
