<script>
  import { onMount, onDestroy } from "svelte"
  import { fade, fly } from "svelte/transition"
  import { setupApp } from "../stores/store.js"
  import { setupDisplay } from "../stores/store.js"

  export let id

  let plant
  let isLeft, isLeftTop, isTop, isRightTop, isRight, isRightBottom, isBottom, isLeftBottom

  onMount(() => {
    plant.style.left = `${(id % setupApp.map) * setupApp.size}vmin`
    plant.style.top = `${Math.floor(id / setupApp.map) * setupApp.size}vmin`
  })

  $: {
    isLeft = id % setupApp.map > 0 && $setupDisplay[id - 1] == 200
    isRight = id % setupApp.map < setupApp.map - 1 && $setupDisplay[id + 1] == 200
    isTop = $setupDisplay[id - setupApp.map] == 200
    isBottom = $setupDisplay[id + setupApp.map] == 200
  }
</script>

<style>
  .plant {
    position: absolute;
    background: #28cf28;
    background: url("./assets/grass.png") no-repeat center center;
    background-size: cover;
    width: 5vmin;
    height: 5vmin;
    border-radius: 100%;
    transition: border-radius 1s;
  }
  .have-left {
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
  }
  .have-right {
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
  }
  .have-top {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
  .have-bottom {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
</style>

<div
  class="plant
    {isLeft ? 'have-left' : ''}
    {isRight ? 'have-right' : ''}
    {isTop ? 'have-top' : ''}
    {isBottom ? 'have-bottom' : ''}"
  in:fade="{{ delay: id, duration: 500, x: Math.sign(Math.random() - 0.5) * 2, y: Math.sign(Math.random() - 0.5) * 2 }}"
  out:fade="{{ duration: 125, x: 0, y: 10 }}"
  bind:this="{plant}"
></div>
