<script>
  import { onMount } from "svelte"
  import { spring } from "svelte/motion"
  import { fade, fly } from "svelte/transition"
  import { setupApp, setupDisplay, setupPersonage, setupScore, doReset } from "./stores/store.js"
  import Field from "./items/Field.svelte"

  Number.prototype.pad = function (size) {
    var s = String(this)
    while (s.length < (size || 2)) {
      s = "0" + s
    }
    return s
  }

  let score = spring(
    { current: 0, left: 0 },
    {
      stiffness: 0.01,
      damping: 0.9,
    }
  )

  let container

  let isWon = false
  let isVisible = false
  
  setupScore.set({
    current: 0,
  })

  $: {
    score.set({ current: $setupScore.current })
  } 

  onMount(() => {
    isVisible = true
    container.style.height = `${window.innerHeight}px`
    window.addEventListener("resize", function (event) {
      container.style.height = `${window.innerHeight}px`
      doReset.set(true)
    })
  })
</script>

<style>
  /* OVERALL VIEW */
  :root {
    --widget-border-radius: 5px;
    --widget-background: rgba(16, 16, 16, 0.8);
    --widget-background-blur-amount: 10px;
    --widget-background-blur: blur(10px);

    --color-title: rgb(186, 225, 225);
    --color-max: #fff;
    --color-middle: #aaa;
    --color-dark: gray;
    --color-basic: #eee;
    --color-active: rgb(200, 200, 137);
    --color-warning: #f6f18b;
    --color-border: silver;
  }
  :global(html) {
    -webkit-text-size-adjust: 100%;
  }
  :global(*) {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
  }

  .container {
    background: #1b2229;
    background: url("./assets/concrete.jpg") center center;
    background-size: 15vmin;
    background-repeat: repeat;
    display: flex;
    width: 100vw;
    height: 100vh;
    align-items: center;
    justify-content: center;
  }
  .header {
    display: flex;
    position: absolute;
    top: 0;
    width: 100%;
    justify-content: space-between;
    font-family: "Press Start 2P", cursive;
    font-size: 150%;
  }
  .header > * {
    color: #f0f0f0;
    padding: 1em;
  }
  .won {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    backdrop-filter: blur(1vmin);
    -webkit-backdrop-filter: blur(1vmin);
    font-family: "Press Start 2P", cursive;
    color: #fff;
    text-align: center;
    z-index: 1;
  }
  .won .title {
    font-size: clamp(1rem, 3vw, 3.5rem);
  }
  .won .score {
    font-size: clamp(2rem, 6vw, 7rem);
  }
</style>

<svelte:head>
  <style>
    @import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap");
  </style>
</svelte:head>

<div class="container" bind:this="{container}">
  <div class="header">
    <div class="score">
      {Math.floor($score.current).pad(5)}
    </div>
  </div>
  {#if isWon}
    <div class="won" transition:fly="{{ duration: 1000, x: 50, y: -50 }}">
      <div class="title">
        CONGRATULATIONS!<br />
        YOU WON!
      </div>
      <div class="score">
        {$setupScore.current.pad(5)}
      </div>
    </div>
  {/if}
  {#if isVisible && !isWon}
    <Field />
  {/if}
</div>
