<script>
  import { onMount, onDestroy } from "svelte"
  import { fade, fly } from "svelte/transition"
  import { setupApp, setupDisplay, setupPersonage, setupScore, doReset, xyPersonage } from "../stores/store.js"

  let player
  let x, y, prevX, prevY
  let touchStartCoords
  let touchNowCoords

  let lastGoLeft = false

  let isVisible = true
  let isAnimated = false
  let timerAnimated

  doReset.subscribe((item) => {
    if (item) {
      setTimeout(() => doReset.set(false), setupApp.speed / 2)
    }
  })
  
  setupPersonage.subscribe((item) => {
    x = item.x
    y = item.y

    if (prevX == undefined) prevX = x
    if (prevY == undefined) prevY = y

    if (!player) return

    player.style.left = `${(x * setupApp.size)}vmin`
    player.style.top = `${(y * setupApp.size)}vmin`
    if (prevX != x || prevY != y) {
      if (x < prevX) {
        lastGoLeft = true
      } else if (x > prevX) lastGoLeft = false

      isAnimated = true
      if (timerAnimated) clearTimeout(timerAnimated)
      timerAnimated = setTimeout(() => (isAnimated = false), 500)
      prevX = x
      prevY = y
    }
  })

  onMount(() => {
    player.style.left = `${-1 * setupApp.size}vmin`
    player.style.top = `${-1 * setupApp.size}vmin`
  })

  const movePosition = (dx, dy) => {
    x = Math.min(Math.max(0, (x += dx)), setupApp.map - 1)
    y = Math.min(Math.max(0, (y += dy)), setupApp.map - 1)
    setupPersonage.set({ x: x, y: y })

    let display = $setupDisplay
    const whatOnPlace = display[x + y * setupApp.map]

    if ($setupScore.gotDiamonds < $setupScore.diamonds && whatOnPlace > 100) {
      let score = $setupScore
      switch (whatOnPlace) {
        case 200:
          score.gotPlants += 1
          score.current = Math.max(score.current - score.plants, 0)
          break
        case 300:
          score.gotDiamonds += 1
          score.current = score.current + score.diamonds
          break
      }
      setupScore.set(score)
    }

    display[x + y * setupApp.map] = 100
    setupDisplay.set(display)
  }

  let timerX, timerY

  const leftGo = () => {
    if (timerX || timerY) return
    timerX = setInterval(() => {
      movePosition(-1, 0)
    }, setupApp.speed)
    movePosition(-1, 0)
  }
  const rightGo = () => {
    if (timerX || timerY) return
    timerX = setInterval(() => {
      movePosition(+1, 0)
    }, setupApp.speed)
    movePosition(+1, 0)
  }
  const upGo = () => {
    if (timerX || timerY) return
    timerY = setInterval(() => {
      movePosition(0, -1)
    }, setupApp.speed)
    movePosition(0, -1)
  }
  const downGo = () => {
    if (timerX || timerY) return
    timerY = setInterval(() => {
      movePosition(0, +1)
    }, setupApp.speed)
    movePosition(0, +1)
  }
  const xStop = () => {
    clearInterval(timerX)
    timerX = undefined
  }
  const yStop = () => {
    clearInterval(timerY)
    timerY = undefined
  }

  const callGo = {
    ArrowLeft: leftGo,
    ArrowRight: rightGo,
    ArrowUp: upGo,
    ArrowDown: downGo,
  }
  const callStop = {
    ArrowLeft: xStop,
    ArrowRight: xStop,
    ArrowUp: yStop,
    ArrowDown: yStop,
  }

  document.addEventListener("keydown", (event) => {
    callGo[event.key]?.()
  })

  document.addEventListener("keyup", (event) => {
    callStop[event.key]?.()
  })

  document.addEventListener("touchstart", (event) => {
    touchStartCoords = { x: Math.floor(event.touches[0].clientX), y: Math.floor(event.touches[0].clientY) }
  })

  document.addEventListener("touchmove", (event) => {
    touchNowCoords = { x: Math.floor(event.touches[0].clientX), y: Math.floor(event.touches[0].clientY) }

    if (touchNowCoords.x < touchStartCoords.x - setupApp.touchPadding) callGo.ArrowLeft()
    if (touchNowCoords.x > touchStartCoords.x + setupApp.touchPadding) callGo.ArrowRight()
    if (touchNowCoords.y < touchStartCoords.y - setupApp.touchPadding) callGo.ArrowUp()
    if (touchNowCoords.y > touchStartCoords.y + setupApp.touchPadding) callGo.ArrowDown()
  })

  document.addEventListener("touchend", (event) => {
    xStop()
    yStop()
  })
</script>

<style>
  .personage {
    position: absolute;
    background: url("./assets/personage.png") no-repeat;
    background-position: -1% center;
    background-size: cover;
    width: 5vmin;
    height: 5vmin;
    transition: background-position 0.5s, left 0.33s linear, top 0.33s linear;
    animation: filter-stand 1s infinite;
    transform-origin: bottom;
  }
  .go {
    -webkit-animation: filter-animation 1s infinite;
    animation: filter-animation 1s infinite;
  }
  .go-invert {
    transform: scaleX(-1);
  }
  .reset {
    animation: none !important;
    transition: none !important;
  }

  @keyframes filter-stand {
    0% {
      transform: scaleY(1);
    }
    50% {
      transform: scaleY(1.05);
    }
    100% {
      transform: scaleY(1);
    }
  }

  @-webkit-keyframes filter-animation {
    0% {
      background-position: 32% center;
    }
    24% {
      background-position: 32% center;
    }
    25% {
      background-position: 64% center;
    }
    49% {
      background-position: 64% center;
    }
    50% {
      background-position: 98% center;
    }
    74% {
      background-position: 98% center;
    }
    75% {
      background-position: 64% center;
    }
    100% {
      background-position: 64% center;
    }
  }
  @keyframes filter-animation {
    0% {
      background-position: 32% center;
    }
    24% {
      background-position: 32% center;
    }
    25% {
      background-position: 64% center;
    }
    49% {
      background-position: 64% center;
    }
    50% {
      background-position: 98% center;
    }
    74% {
      background-position: 98% center;
    }
    75% {
      background-position: 64% center;
    }
    100% {
      background-position: 64% center;
    }
  }
</style>

{#if isVisible}
  <div
    class="personage
    {$doReset ? 'reset' : ''} 
    {isAnimated ? 'go' : ''} 
    {lastGoLeft ? 'go-invert' : ''}"
    bind:this="{player}"
  ></div>
{/if}
