<script>
  import { get, writable } from "svelte/store"
  import { setupApp, setupDisplay, setupPersonage, doReset, setupScore } from "../stores/store.js"
  import gsap, { Linear } from "gsap"
  import { MotionPathPlugin } from "gsap/MotionPathPlugin"

  import Ruby from "./Ruby.svelte"
  import Plant from "./Plant.svelte"
  import Ground from "./Ground.svelte"
  import Personage from "./Personage.svelte"
  import { onMount } from "svelte"

  gsap.config({
    force3D: false,
    nullTargetWarn: false,
    trialWarn: false,
    units: {x: "px", y: "px", left: "%", top: "%", rotation: "rad"}
  });
  gsap.registerPlugin(MotionPathPlugin)
  let groundroad

  const plots = 48
  const duration = 15
  const road = [ { x:-1, y:-1 }, { x:0,y:0 }, { x:2,y:4}, { x:7,y:10}, { x:10,y:4 }, { x:15,y:15 }, { x:16,y:16 } ]
  let dots = [], rubys = []
  let tl, tr, reRoad

  const recalculateRoad = () => {

    // NEW EDGE COORDINATES
    const getNextPosition = (previous) => {
      let nextPosition = Math.min(Math.max(0, Math.floor(previous + 1 + (Math.random() * 4))), setupApp.map - 1)
      return nextPosition
    }
    let position = { ...road[0] }
    for (let i = 1; i < 6; i++) {
      road[i] = {
        x: getNextPosition(road[i-1].x),
        y: getNextPosition(road[i-1].y)
      }
    }
    setupPersonage.set(position)

    // REDRAW RUBYS
    rubys.length = 0
    setTimeout(() => rubys = [ ...road ], 0)

    // REDRAW ROAD
    dots.forEach((dot) => {
      dot.parentElement.removeChild(dot)
    })
    dots.length = 0

    if (!tr) {
      tr = new gsap.timeline()
    } else {
      tr.clear()
      tr.seek(0)
    }
    let counter = 0
    tr.to(position, { duration: 0.5, motionPath: { path: road, curviness: 1 }, ease: `steps(${plots})`, immediateRender: true, onUpdate() { 
      counter++
      const dot = document.createElement("div")
      dot.id = `ground_${counter}`
      dot.classList.add('ground-plot')
      dot.style.left = `${position.x * setupApp.size}vmin`
      dot.style.top = `${position.y * setupApp.size}vmin`
      groundroad.appendChild(dot)
      dots.push(dot)
    } } )

    // RUN THE PERSONAGE
    if (!tl) {
      tl = new gsap.timeline({ repeat: -1, onRepeat: () => { 
        doReset.set(true)
        reRoad = undefined
        setTimeout(() => recalculateRoad(), 0)
      } })
    } else {
      tl.clear()
      tl.seek(0)
    }
    tl.to(position, { duration, motionPath: { path: road, curviness: 1 }, ease: 'none', onUpdate() { 
      setupPersonage.set(position)

      if (rubys.length !== 0) {
        const roundX = Math.floor(position.x)
        const roundY = Math.floor(position.y)
        const foundRuby = rubys.findIndex((ruby) => roundX !== -1 && ruby.x == roundX && ruby.y == roundY)
        if (foundRuby > -1) {
          rubys[foundRuby].removed = true
          setupScore.set({ current: $setupScore.current+1 })
        }
      }
    } })
  }
  
  onMount(() => {
    setTimeout(() => recalculateRoad(), 100)
  })

</script>

<style>
  .field {
    position: relative;
    width: calc(16 * 5vmin);
    height: calc(16 * 5vmin);
    border-radius: 1vmin;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    overflow: hidden;
    box-shadow: rgb(10, 10, 10) 0vmin 0vmin 10vmin;
  }
  .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .road {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    filter: blur(32px);
  }
  :global(.ground-plot) {
    position: absolute;
    width: 15vmin;
    height: 15vmin;
    background-color: #000;
    border-radius: 50%;
  }
</style>

<div class="field">
  
  <div class="road" bind:this={groundroad}></div>

  <div class="overlay">
    <Personage id="{0}" mode="{0}" />
  </div>

  {#if rubys.length !== 0}
    {#each rubys as item, i}
      {#if (i>0) && (i<6)}
        {#if !item.removed }
          <Ruby id="{i}" mode="{item}" />
        {/if}
      {/if}
    {/each}
  {/if}



  <!-- {#each $setupDisplay as item, i}
    {#if item == 300}
      <Ruby id="{i}" mode="{item}" />
    {:else if item == 200}
      <Plant id="{i}" mode="{item}" />
    {:else}
      <Ground id="{i}" mode="{item}" />
    {/if}
  {/each}
  <div class="overlay">
    <Personage id="{0}" mode="{0}" />
  </div -->
</div>
